/**
 * api
 */
import request from '@/utils/request'

// 

export function login(code) {
	return request({
		url: '/aioveu-auth/oauth2/token',
		method: 'post',
		params: {
			code: code,
			grant_type: 'wechat'
		},
		headers: {
			'Authorization': 'Basic bWFsbC1hcHA6MTIzNDU2' // Base64mall-app:123456
		}
	})
}


// H5/Android/IOS 



















export function logout() {
	return request({
		url: '/aioveu-auth/oauth/logout',
		method: 'delete',
		headers: {
			'auth': true // 
		}
	})
}

export function getUserInfo() {
	return request({
		url: '/aioveu-ums/app-api/v1/members/me',
		method: 'get',
		headers: {
			'auth': true
		}
	})
}

export function sendSmsCode(phoneNumber) {
	return request({
		url: '/aioveu-auth/api/v1/auth/sms_code',
		method: 'post',
		params: {
			mobile: phoneNumber
		}
	})
}