import request from '@/utils/request'

// 
export function listOrdersWithPage(params) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders',
		method: 'get',
		params: params,
		headers: {
			'auth': true // 
		}
	})
}

// 
export function confirm(skuId) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders/confirm',
		method: 'post',
		params: {
			skuId: skuId
		},
		headers: {
			'auth': true // 
		}
	})
}

// 
export function submit(data) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders/submit',
		method: 'post',
		data: data,
		headers: {
			'auth': true // 
		}
	})
}

// 
export function pay(data) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders/payment',
		method: 'post',
		data:data,
		headers: {
			'auth': true // 
		}
	})
}

// 
export function cancelOrder(orderId) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders/cancel',
		method: 'put',
		params: {
			id: orderId
		},
		headers: {
			'auth': true // 
		}
	})
}

// 
export function deleteOrder(orderId) {
	return request({
		url: '/aioveu-oms/app-api/v1/orders/' + orderId,
		method: 'delete',
		headers: {
			'auth': true // 
		}
	})
}
