import request from '@/utils/request'


// 
export function getCart() {
	return request({
		url: '/aioveu-oms/app-api/v1/carts',
		method: 'get',
		headers: {
			'auth': true // 
		}
	})
}

/**
 * /
 * @param {Object} params
 */
export function checkAll(params) {
	return request({
		url: '/aioveu-oms/app-api/v1/carts/_check',
		method: 'patch',
		params: params,
		headers: {
			'auth': true
		}
	})
}


// 
export function deleteCart() {
	return request({
		url: '/aioveu-oms/app-api/v1/carts',
		method: 'delete',
		headers: {
			'auth': true
		}
	})
}


// 
export function addCartItem(skuId) {
	return request({
		url: '/aioveu-oms/app-api/v1/carts',
		method: 'post',
		params: {
			skuId: skuId
		},
		headers: {
			'auth': true
		}
	})
}

// 
export function updateCartItem(skuId, data) {
	return request({
		url: '/aioveu-oms/app-api/v1/carts/skuId/' + skuId,
		method: 'put',
		data: data,
		headers: {
			'auth': true
		}
	})
}


// 
export function removeCartItem(skuId) {
	return request({
		url: '/aioveu-oms/app-api/v1/carts/skuId/' + skuId,
		method: 'delete',
		headers: {
			'auth': true
		}
	})
}
